<template>
  <div class="mv">
    <van-tabs v-model="active" @click="onChange">
      <van-tab title="全部"></van-tab>
      <van-tab title="内地"></van-tab>
      <van-tab title="港台"></van-tab>
      <van-tab title="欧美"></van-tab>
      <van-tab title="日本"></van-tab>
      <van-tab title="韩国"></van-tab>
    </van-tabs>
    <div class="mv-box">
      <div
        class="mv-list"
        v-for="(item,index) in mvList"
        :key="index"
        @click="onClick(item.id,item.name)"
      >
        <div class="mv-img">
          <img class="cover" :src="item.cover" />
          <img class="play" src="../../assets/images/mv-play.png" />
        </div>
        <div class="mv-name">{{item.name}}</div>
        <div class="artistName">{{item.artistName}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  data() {
    return {
      active: 0,
      offset: 1,
      area: '全部',
      mvList: [],
    }
  },
  mounted() {
    this.mvall()
  },
  methods: {
    mvall() {
      Toast.loading({
        message: '加载中...',
        forbidClick: true,
        overlay: true,
      })
      let data = {
        limit: 30,
        offset: this.offset,
        area: this.area,
      }
      this.api
        .mvall(data)
        .then((res) => {
          if (res.data.code == 200) {
            this.mvList = res.data.data
          } else {
            Toast(res.data.msg)
          }
        })
        .catch((err) => {
          Toast(err)
        })
    },
    onChange(name, title) {
      this.area = title
      this.mvall()
    },
  },
}
</script>

<style lang="less" scoped>
.mv {
  padding: 0 30px;
  margin-bottom: 100px;

  .mv-box {
    padding: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .mv-list {
      width: 46%;
      height: 290px;
      border-radius: 15px;
      box-shadow: 4px 4px 8px 2px rgba(0, 0, 0, 0.05);
      margin-bottom: 20px;
      padding: 10px;

      .mv-img {
        position: relative;

        .play {
          width: 80px;
          height: 80px;
          position: absolute;
          top: 50%;
          left: 50%;
          margin-top: -40px;
          margin-left: -40px;
        }

        .cover {
          width: 100%;
          height: 200px;
          object-fit: fill;
          border-radius: 10px;
        }
      }
      .mv-name {
        font-size: 26px;
        font-weight: bold;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
        margin-top: 8px;
        padding: 0 5px;
      }

      .artistName {
        font-size: 22px;
        color: rgba(0, 0, 0, 0.5);
        margin-top: 4px;
        padding: 0 5px;
      }
    }
  }
}
</style>